/* Base styles for larger screens */
.relatedproducts {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    height: 90vh;
}

.relatedproducts h1 {
    color: #171717;
    font-size: 50px;
    font-weight: 600;
}

.relatedproducts hr {
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: #252525;
}

.relatedproducts-item {
    margin-top: 50px;
    display: flex;
    gap: 30px;
}

/* Media Queries for Responsive Design */
@media screen and (max-width: 768px) {
    .relatedproducts {
        height: auto;
    }

    .relatedproducts h1 {
        font-size: 25px;
    }

    .relatedproducts hr {
        width: 140px;
        height: 2px;
    }

    .relatedproducts-item {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
    }
}
