.navbar {
    display: flex;
    justify-content: space-around;
    padding: 9px;
    box-shadow: 0 1px 3px -2px black;
    align-items: center;
}
.nav-logo {
    display: flex;
    align-items: center;
    gap: 10px;
}
.nav-logo img {
    width: 45px;
}
.nav-logo p {
    color: #171717;
    font-size: 28px;
    font-weight: 600;
}
.nav-menu {
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    color: #626262;
    font-size: 18px;
    font-weight: 500;
}

.nav-menu li {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3px;
    cursor: pointer;
}
.nav-menu hr {
    border: none;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    background: #ff4141;
}
.nav-login-cart {
    display: flex;
    align-items: center;
    gap: 45px;
}
.nav-login-cart img {
    width: 30px;
}

.nav-login-cart button {
    width: 110px;
    height: 40px;
    outline: none;
    border: 1px solid #7a7a7a;
    border-radius: 75px;
    color: #515151;
    font-size: 18px;
    font-weight: 500;
    background: white;
    cursor: pointer;
}
.nav-login-cart button:active {
    background: #f3f3f3;
}
.nav-cart-count {
    width: 18px;
    height: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -35px;
    margin-left: -55px;
    border-radius: 11px;
    font-size: 12px;
    background: red;
    color: white;
}
 .menu-button {
    display: none;
    flex-direction: column;
    background: transparent;
    border: none;
    cursor: pointer;
    margin: 0;
    padding: 0;
    outline: none;
  }
  
  .bar {
    background: #211919;
    height: 3px;
    width: 25px;
    margin: 4px 20px;
    transition: 0.4s;
    margin-right: 0;
  }
  
  .menu-button.open .bar:nth-child(1) {
    transform: rotate(-45deg) translate(-5px, 6px);
  }
  
  .menu-button.open .bar:nth-child(2) {
    opacity: 0;
  }
  
  .menu-button.open .bar:nth-child(3) {
    transform: rotate(45deg) translate(-5px, -6px);
  }
@media(max-width: 1280px) {
    .navbar {
        padding: 12px 50px;
    }
    .nav-logo img {
        width: 40px;
    }
    .nav-logo p {
        font-size: 25px;
    }
    .nav-menu {
        gap: 30px;
        font-size: 16px;
    }
    .nav-login-cart {
        gap: 30px;
    }
    .nav-login-cart button {
        width: 120px;
        gap: 45px;
        font-size: 16px;
    }
    .nav-cart-count {
        margin-left: -40px;
        font-size: 12px;
    }
}

@media(max-width:1024px) {
    .navbar {
        padding: 12px 30px;
    }
    .nav-menu {
        gap: 25px;
        font-size: 14px;
    }
    .nav-login-cart button {
        width: 80px;
        height: 35px;
        font-size: 14px;
    }
    .nav-login-cart img {
        width: 30px;
    }
    .nav-cart-count {
        width: 18px;
        height: 18px;
    }
}

@media(max-width:800px) {
    .navbar {
        padding: 10px 0;
    }
    .nav-menu {
        display: none;
        height: 80px;
        width: 100%;
        position: absolute;
        background-color: white;
        justify-content: center;
        top: 50px;
    }
    .nav-menu-visible {
        display: flex;
    }
    .menu.open {
        transform: scaleY(1);
    }

    .menu-button {
        display: flex;
    }
}

@media(max-width:500px) {
    .nav-logo {
        transform: scale(0.8);
    }
    .nav-menu {
        height: 250px;
        flex-direction: column;
        position: absolute;
        top: 60px;
        left: 0;
        right: 0;
        background-color: white;
        transition: transform 0.3s ease-in-out;
    }
    .nav-login-cart {
        transform: scale(0.8);
    }
    .menu.open {
        transform: scaleY(1);
      }
    
      .menu-button {
        display: flex;
      }
}